import symbolMixin from '@/mixins/symbols.js';

export default {
    data() {
        return {
            user: true,
            sidenav: false,
        }
    },
    mixins: [symbolMixin],
    methods: {
        opensidenav() {
            this.sidenav ? this.sidenav = false : this.sidenav = true;
        }
    }
}
